/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable require-jsdoc */
import { Component, Inject } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { ChqWidgetsInputModel, IControlValue } from 'src/app/model/chq-widgets-input-model';
import { CommonService } from 'src/app/services/common/common.service';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'chr-booking-message',
  templateUrl: './booking-message.component.html',
  styleUrls: [ './booking-message.component.scss' ]
})
export class BookingMessageComponent {

  public messageForm = this.formBuilder.group({
    Type: [ '', Validators.required ],
    Email: [ '', Validators.required ],
    Subject: [ '', Validators.required ],
    Message: [ '', Validators.required ]
  });

  messageModal: ChqWidgetsInputModel[] = [
    {
      placeHolder: 'Type',
      name: 'Type',
      label: 'type',
      value: '',
      type: 'radio',
      options: [
        { label: 'Insurance', value: 'Insurance', checked: false },
        { label: 'Driver', value: 'Driver', checked: false }
      ]
    },
    {
      placeHolder: 'Email Address',
      label: 'email_address',
      name: 'Email',
      value: '',
      type: 'text',
      suffix: 'CC BCC'
    },
    {
      placeHolder: 'Subject',
      name: 'Subject',
      label: 'subject',
      value: '',
      type: 'text'
    },
    {
      placeHolder: 'Message',
      name: 'Message',
      label: 'message',
      value: '',
      type: 'textarea'
    }
  ];

  public attachmentButton: ChqWidgetsButtonModel = {
    label: 'add_attachment',
    type: 'outline',
    icon: 'attachment',
    iconColor: 'black'
  }

  public sendButton: ChqWidgetsButtonModel = {
    label: 'send',
    type: this.messageForm.valid ? 'primary' : 'disabled',
    icon: 'send',
    iconColor: 'white'
  }

  constructor(
    public dialogRef: MatDialogRef<BookingMessageComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private formBuilder: FormBuilder,
    private commonService: CommonService
  ) { }

  onFormUpdate(output: IControlValue): void {
    this.messageForm.get(output.name).setValue(output.value);
  }

  onSubmit(): void {
    console.log(this.messageForm);
  }
}