import { CommonModule, CurrencyPipe, DatePipe } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDialogModule } from '@angular/material/dialog';
import { MatIconModule } from '@angular/material/icon';
import { MatMenuModule } from '@angular/material/menu';
import { MatSidenavModule } from '@angular/material/sidenav';
import { MatSlideToggleModule } from '@angular/material/slide-toggle';
import { MatSliderModule } from '@angular/material/slider';
import { MatTooltipModule } from '@angular/material/tooltip';
import { Route, RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { DxDraggableModule, DxSchedulerModule, DxScrollViewModule, DxTemplateModule, } from 'devextreme-angular';
import { DxoScrollingModule } from 'devextreme-angular/ui/nested';
import { DirectiveModule } from 'src/app/helper/directive.module';
import { IconsModule } from 'src/app/icons.module';
import { VersionControlResolver } from 'src/app/resolvers/versionControl.resolver';
import { WidgetsModule } from 'src/app/widgets/chq-widgets.module';
import { BookingJournalComponent } from './booking-journal/booking-journal.component';
import { BookingMessageComponent } from './booking-message/booking-message.component';
import { BookingPreviewComponent } from './booking-preview/booking-preview.component';

const casesRoutes: Route[] = [
  {
    path: '',
    component: BookingJournalComponent,
    resolve: { version: VersionControlResolver }
  }
];

@NgModule({
  declarations: [
    BookingJournalComponent,
    BookingPreviewComponent,
    BookingMessageComponent
  ],
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    MatIconModule,
    IconsModule,
    RouterModule.forChild(casesRoutes),
    MatMenuModule,
    DxSchedulerModule,
    DxTemplateModule,
    DxDraggableModule,
    DxScrollViewModule,
    DxoScrollingModule,
    WidgetsModule,
    MatSidenavModule,
    MatSliderModule,
    MatDialogModule,
    MatSlideToggleModule,
    MatTooltipModule,
    TranslateModule,
    DirectiveModule
  ],
  providers: [
    DatePipe,
    CurrencyPipe
  ]
})
export class BookingJournalModule { }
